<style lang="scss" scoped>
  .container{
    padding-top: 50px;
    font-size: 13px;
    dt{
      background-color: rgba(47, 134, 224, 0.26);
      color: #fefefe;
      height: 48px;
      line-height: 48px;
      padding: 0 15px;
      i{
        font-style: normal;
        margin-right: 10px;
      }
    }
    dd{
      padding: 15px;
      margin: 4px 0 30px;
      height: auto;
      background-color: rgba(6, 98, 211, 0.2);
    }
    .charts{
      height: 468px;
      width: 100%;
    }
  }

</style>
<template>
<div class="container">
  <dl>
    <dt>
      <i class="iconfont icon-zhongjianjian"></i>
      <i>个人信息</i>
    </dt>
    <dd>
      <div class="charts" id="chartsPinfo"></div>
    </dd>
    <dt>
      <i class="iconfont  icon-jishuikeng"></i>
      <i>水坑</i>
    </dt>
    <dd>
      <div class="charts" id="chartsWater"></div>
    </dd>
    <dt>
      <i class="iconfont icon-gongjimubiao"></i>
      <i>攻击</i>
    </dt>
    <dd>
      <div class="charts" id="chartsAttract"></div>
    </dd>
  </dl>
</div>
</template>
<script type="text/ecmascript-6">
  import echarts from 'echarts/lib/echarts';
  export default {
    data () {
      return {
        page: "recognise",
        chartsPinfo: null,
        chartsWater: null,
        chartsAttract: null

      }
    },
    created () {
    },
    mounted () {
      let chartsPinfo = document.getElementById('chartsPinfo');
      let chartsAttract = document.getElementById('chartsAttract');
      let chartsWater = document.getElementById('chartsWater');
      this.chartsPinfo = echarts.init(chartsPinfo);
      this.chartsAttract = echarts.init(chartsAttract);
      this.chartsWater = echarts.init(chartsWater);
      this.getPropsData();
      let self = this;
      window.addEventListener("resize", function () {
        self.chartsPinfo.resize();
        self.chartsAttract.resize();
        self.chartsWater.resize();
      });
    },
    methods: {
      getPropsData: function (apiStr){
//        this.apiGet(api_str).then().catch()
        let data1 = {
          x: ['2017.5.1', '2017.5.2', '2017.5.3', '2017.5.4', '2017.5.5', '2017.5.6', '2017.5.7', '2017.5.8', '2017.5.9', '2017.5.10', '2017.5.11', '2017.5.12'],
          y: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3]
        }
        let data2 = {
          x: ['2017.5.1', '2017.5.2', '2017.5.3', '2017.5.4', '2017.5.5', '2017.5.6', '2017.5.7', '2017.5.8', '2017.5.9', '2017.5.10', '2017.5.11', '2017.5.12'],
          y: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3]
        }
        let data3 = {
          x: ['2017.5.1', '2017.5.2', '2017.5.3', '2017.5.4', '2017.5.5', '2017.5.6', '2017.5.7', '2017.5.8', '2017.5.9', '2017.5.10', '2017.5.11', '2017.5.12'],
          y: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3]
        }
        this.chartsPinfo.setOption(this.setOption1(data1));
        this.chartsAttract.setOption(this.setOption2(data2));
        this.chartsWater.setOption(this.setOption3(data3));
      },
      setOption1: function (data){
        return {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            }
          },
          xAxis: [
            {
              type: 'category',
              data: data['x'],
              axisLine: {
                lineStyle: {
                  color: 'white'
                }
              },
              axisPointer: {
                type: 'shadow'
              }
            }
          ],
          yAxis: [
            {
              type: 'value',

              axisLine: {
                lineStyle: {
                  color: 'white'
                }
              },
              min: 0,
              max: 400,
              interval: 100,
              axisLabel: {
                formatter: '{value} '
              }
            }
          ],
          grid: {
            left: '35', // grid 组件离容器左侧的距离。
            width: '90%'
          },
          series: [
            {
              itemStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#fe8d06'},
                      {offset: 0.5, color: '#fe8d06'},
                      {offset: 1, color: '#f9692a'}
                    ]
                  )
                },
                emphasis: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#f9692a'},
                      {offset: 0.7, color: '#f9692a'},
                      {offset: 1, color: '#fb7b18'}
                    ]
                  )
                }
              },
              type: 'bar',
              data: data['y']
            }
          ]
        };
      },
      setOption2: function (data){
        return {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            }
          },
          xAxis: [
            {
              type: 'category',
              data: data['x'],
              axisLine: {
                lineStyle: {
                  color: 'white'
                }
              },
              axisPointer: {
                type: 'shadow'
              }
            }
          ],
          yAxis: [
            {
              type: 'value',

              axisLine: {
                lineStyle: {
                  color: 'white'
                }
              },
              min: 0,
              max: 400,
              interval: 100,
              axisLabel: {
                formatter: '{value} '
              }
            }
          ],
          grid: {
            left: '35', // grid 组件离容器左侧的距离。
            width: '90%'
          },
          series: [
            {
              itemStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#83bff6'},
                      {offset: 0.5, color: '#188df0'},
                      {offset: 1, color: '#188df0'}
                    ]
                  )
                },
                emphasis: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#2378f7'},
                      {offset: 0.7, color: '#2378f7'},
                      {offset: 1, color: '#83bff6'}
                    ]
                  )
                }
              },
              type: 'bar',
              data: data['y']
            }
          ]
        };
      },
      setOption3: function (data){
        return {
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              crossStyle: {
                color: '#999'
              }
            }
          },
          xAxis: [
            {
              type: 'category',
              data: data['x'],
              axisLine: {
                lineStyle: {
                  color: 'white'
                }
              },
              axisPointer: {
                type: 'shadow'
              }
            }
          ],
          yAxis: [
            {
              type: 'value',

              axisLine: {
                lineStyle: {
                  color: 'white'
                }
              },
              min: 0,
              max: 400,
              interval: 100,
              axisLabel: {
                formatter: '{value} '
              }
            }
          ],
          grid: {
            left: '35', // grid 组件离容器左侧的距离。
            width: '90%'
          },
          series: [
            {
              itemStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#83bff6'},
                      {offset: 0.5, color: '#188df0'},
                      {offset: 1, color: '#188df0'}
                    ]
                  )
                },
                emphasis: {
                  color: new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#2378f7'},
                      {offset: 0.7, color: '#2378f7'},
                      {offset: 1, color: '#83bff6'}
                    ]
                  )
                }
              },
              type: 'bar',
              data: data['y']
            }
          ]
        };
      }
    }
  }
</script>



