<template>
  <div id="loginWrap">
    <div id="loginForm">
      <p class="title">登录到管理系统</p>
      <div class="body">
        <el-form ref="form" :model="form" label-width="55px">
          <el-form-item label="用户名">
            <el-input v-model="form.username"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="form.pwd"></el-input>
          </el-form-item>
          <el-button class="btnSub" type="primary" @click="onSubmit">登录</el-button>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
   import http from '../../assets/js/http'
   export default {
     data () {
       return {
         form: {
           username: '',
           pwd: ''
         }
       }
     },
     methods: {
       onSubmit: function () {
         let formData = this.form;
         if (formData.username === '' || formData.pwd === '') {
           this.$notify.error({
             position: 'top-right',
             message: '用户名和密码不能为空！'
           });
           return;
         }
         this.$http.post(Api('login'), this.form).then(
           res => {
             let userInfo = JSON.stringify(res.body);
             localStorage.setItem('userInfo', userInfo);
             this.$router.replace('/index');
           },
           err => {
             const h = this.$createElement;
             this.$notify.error({
               position: "top-right",
               message: h('b', err.body.message)
             });
           }
         )
       }
     }
   }
</script>
<style lang="stylus">
  #loginWrap
    width: 100%
    height: 100%
    background url('../../assets/image/login.png') 150px 150px no-repeat;
  #loginForm
    box-sizing border-box
    width 300px
    height 320px
    border 1px solid #ddd
    position absolute
    right 200px
    top 50%
    transform translateY(-200px)
    padding 20px
    border-radius 10px
    background #fafafa
    .body
      overflow auto
      padding 15px 25px 0
    .title
      height 80px
      line-height 80px
      text-align center
    .btnSub
      width 100%
      margin-top 30px
</style>




