<template>
  <div class="ab-input-sel-search">
    <div class="ab-input-select">
      <el-select v-model="models" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="ab-input-text">
      <input type="text" placeholder="请输入内容">
    </div>
    <div class="ab-input-button-wrap">
      <div class="button">搜索</div>
      <i class="el-icon-search"></i>
    </div>
  </div>
</template>
<style lang="scss">
  $bg-clolor: #0e385e;
  .ab-input-sel-search{
    position: relative;
    display: flex;
    flex-flow:row nowrap;
    justify-content: stretch;
    align-items: center;
    width: 100%;
    height:auto;
    input,select,.button{
      height: 44px;
      border:none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background-color: #fff;
      color: $bg-clolor;
      font-size:14px;
      & &:focus{
        -ms-scrollbar-highlight-color: rgba(0,0,0,0);
        -webkit-tap-highlight-color:rgba(0,0,0,0);
      }
    }
    .ab-input-select{
      position: absolute;
      left:0;
      top:50%;
      transform:translateY(-50%);
    }
    .ab-input-text{
      width: 100%;
      padding:0 88px 0 100px;
      input{
        padding:0 15px;
        width: 100%;
      }
    }
    .ab-input-button-wrap{
      position: absolute;
      right:0;
      top:50%;
      cursor: pointer;
      transform:translateY(-50%);
      &:before{
        width:0;
        height:25px;
        position: absolute;
        top:9px;
        border:1px solid $bg-clolor;
        content: "";
      }
      .button{
        width:88px;
        line-height: 44px;
        padding:0 20px 0 40px;
        cursor:pointer;
      }
      .el-icon-search{
        color:$bg-clolor;
        position: absolute;
        left:15px;
        top:35%;
      }
    }
    .el-input {
      width:100px !important;
      input{
        border-radius: 0 !important;
        height: 44px !important;
      }
    }
  }
  .el-select-dropdown__item{
    &.selected,&.hover{
      color:#0e385e !important;
    }
  }
  .popper__arrow{
    left:55px !important;
    color:$bg-clolor;
  }

</style>
<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        models: '社工',
        options: [
          {
            value: '1',
            label: '社工'
          },
          {
            value: '2',
            label: '漏洞'
          },
          {
            value: '3',
            label: '业务'
          }
        ]
      }
    }
  }
</script>



