import ol from 'openlayers'

class Map{
  constructor () {
    let projection = new ol.proj.Projection({
      code: 'EPSG:900913',
      units: 'm',
      global: true,
      extent: [-20037508, -20037508, 20037508, 20037508]
    });
    let theView = new ol.View({
      projection: projection,
      extent: [-20037508.34, -10037508.34, 20037508.34, 20037508.340000015],
      maxZoom: 19,
      // minZoom: 1,
      center: [10781798.76265623, 5596413.462148452],
      zoom: 1
    });
    let tileEN = new ol.source.XYZ(/** @type {olx.source.TileWMSOptions} */({
      url: `http://192.168.20.14:8888/styles/NewStyleEN/rendered/{z}/{x}/{y}.png`
    }));
    let layerEN = new ol.layer.Tile({
      source: tileEN
    })
    let map = new ol.Map({
      controls: ol.control.defaults({
        attribution: false,
        rotate: false,
        zoom: false
      }),
      layers: [layerEN],
      loadTilesWhileAnimating: true,
      loadTilesWhileInteracting: true,
      view: theView
    });
    // map.addControl(new ol.control.ScaleLine());
    map.on('pointermove', function (evt) {
      map.getTargetElement().style.cursor =
          map.hasFeatureAtPixel(evt.pixel) ? 'pointer' : '';
    });
    // map.setTarget('map')
    window.addEventListener('resize', () => { map.updateSize() }, false)
    map.on('moveend', function () {
      let differenceTop = 20037508 - (map.getView().getResolution()) * (map.getSize()[1] / 2);
      let differenceBottom = 20037508 - (map.getView().getResolution()) * (map.getSize()[1] / 2);
      let differentLeft = 65247864 - (map.getView().getResolution()) * (map.getSize()[0] / 2);
      let differentRight = 65247864 - (map.getView().getResolution()) * (map.getSize()[0] / 2);
      let zoom = map.getView().getZoom();
      let center = map.getView().getCenter();
      theView = new ol.View({
        projection: projection,
        maxZoom: 20,
        minZoom: 2,
        center: center,
        zoom: zoom,
        extent: [-differentLeft, 0 - differenceBottom, differentRight, differenceTop]
      });
      map.setView(theView);
    });
    map.on('click', function (evt) {
      if (infoSwitch == 'open') {
        let fea = map.forEachFeatureAtPixel(evt.pixel,
          function (feature) {
            return feature;
          });
        if (fea) {
          console.log('feature')
          let feature;
          if (fea.get('features') && fea.get('features').length == 1) {
            feature = fea.get('features')[0];
          } else {
            feature = fea;
          }
          let coordinates = feature.getGeometry().A;
          if (feature.get('remark') == 'point') {
            let info = new InfoWindow({
              offset: [0, -50],
              content: feature.get('name_en')
            });
            info.close();
            info.open(coordinates);
          } else if (feature.get('remark') == 'area') {
            let info = new InfoWindow({
              offset: [0, 0],
              content: feature.get('name_en')
            });
            info.close();
            info.open(coordinates);
          } else if (feature.get('remark') == 'click') {
            console.log('featureClick')
            var code = feature.get('function');
            code();
          }
        }
      }
    });
    let InfoWindow = (function () {
      // 静态私有变量
      let overlays = [];
      let infoRecords = [];
      let infoBoxPointCount = 0;
          // 构造函数
      let newFun = function (data) {
        if (data.closeVisiable == undefined) {
          data.closeVisiable = true
        }
        this.open = function (coordinates, callback) {
          if (infoRecords.indexOf("mark_" + coordinates) == -1) {
            infoBoxPointCount = infoBoxPointCount + 1;
            // 创建信息窗体
            let popup = document.createElement('div');
            popup.setAttribute('id', 'popup' + infoBoxPointCount);
            popup.setAttribute('class', 'ol-popup');
            // 信息窗体标头
            let popupTitle = document.createElement('div');
            popupTitle.setAttribute('id', 'popup-title' + infoBoxPointCount);
            popupTitle.setAttribute('class', 'ol-popup-title');
            if (data.closeVisiable == true) {
              popupTitle.innerHTML = '<span id=' + "infobox_close" + infoBoxPointCount + ' class="ol-popup-closer" remark=' + infoBoxPointCount + ' icon="el-icon-close"></span>'
            }
            popup.appendChild(popupTitle);
            // 信息窗体内容
            let popupContent = document.createElement('div');
            popupContent.setAttribute('id', 'popup-content' + infoBoxPointCount);
            popupContent.setAttribute('class', 'ol-popup-content');
            try {
              popupContent.appendChild(data.content)
            } catch (e) {
              popupContent.innerHTML = data.content;// 信息窗体展示的内容
            }
            console.log('popupContent')
            console.log(data)
            popup.appendChild(popupContent);
            // ol.OverLay
            let overlay = new ol.Overlay(/** @type {olx.OverlayOptions} */ ({
              element: popup,
              stopEvent: false,
              offset: data.offset,
              autoPan: true,
              autoPanAnimation: {
                duration: 450
              }
            }));
            map.addOverlay(overlay);
            overlays.push(overlay);
            overlay.setPosition(coordinates);
            // 添加记录，如果窗体已经存在，就不再出现
            infoRecords.push('mark_' + coordinates);
            // 关闭按钮事件
            document.getElementById('infobox_close' + infoBoxPointCount).addEventListener("click", function () {
              document.getElementById('popup' + this.getAttribute('remark')).parentNode.remove();
              infoRecords.splice(getIndex('mark_' + coordinates, infoRecords), 1);
              if (callback) {
                callback();
              }
            });
            // $("#infobox_close" + infoBoxPointCount).click(function () {
            //   $('#popup' + this.getAttribute('remark')).parent('.ol-overlay-container').remove();
            //   infoRecords.splice(getIndex('mark_' + coordinates, infoRecords), 1);
            //   if (callback) {
            //     callback();
            //   }
            // });
          }
        }
        this.close = function () {
          overlays.forEach(function (e) {
            map.removeOverlay(e);
          })
          // $('.ol-popup').remove();
          infoRecords.length = 0;
          infoBoxPointCount = 0;
          overlays.length = 0;
        }
      }
      let getIndex = function getIndex (val, arr) {
        for (let i = 0; i < arr.length; i++) {
          if (arr[i] == val) return i;
        }
        return -1;
      }
      return newFun;
    })();
    let infoSwitch = `open`;
    // 打开信息窗体
    function openInfoBox () {
      infoSwitch = 'open';
    }
    // 关闭信息窗体
    function closeInfoBox () {
      infoSwitch = 'close';
    };
    // 添加波纹图片
    let DrawPoint = (function () {
      let popupArr = [];
      // 构造函数
      let funDiv = function (data) {
        let local = data.local;
        let style = data.style;
        let color;
        this.showImgMark = function () {
          switch (style) {
            case 'webshell':
              color = '#0ff';
              break;
            case '运营商':
              color = '#06DD3F';
              break;
            case '设备节点':
              color = '#E12726';
              break;
          }
          let div = document.createElement('div');
          div.setAttribute('id', 'imgMark');
          div.innerHTML = '<div class="dot" style="background-color:' + color + ';border:2px solid ' + color + '"></div><div class="pulse" style="border:2px solid ' + color + '"></div><div class="pulse1" style="border:2px solid ' + color + '"></div><div class="pulse2" style="border:2px solid ' + color + '"></div>';
          let popup = new ol.Overlay({
            element: div,
            stopEvent: false,
            offset: data.offset == undefined ? [0, 0] : data.offset
          });
          popup.setPosition(local);
          map.addOverlay(popup);
          popupArr.push(popup);
        }
        this.addClick = function (callback) {
          document.getElementById('imgMark').addEventListener("click", function () {
            callback();
          })
        }
        this.delete = function () {
          popupArr.forEach(function (e) {
            map.removeOverlay(e);
          })
          popupArr.length = 0;
        }
      }
      return funDiv;
    })();
    // 多点点标注类
    let manyMark = (function () {
      let markVectorRes = new ol.source.Vector({
        wrapX: false
      });
      let layerVectorMark = new ol.layer.Vector({
        source: markVectorRes
      })
      // 构造函数
      let mark = function (data) {
        let local = data.local;
        let style = data.style;
        let offset = data.offset;
        let content = data.content;
        let animateSwitch = data.animateSwitch;
        let zoomVisiable = data.zoomVisiable == undefined ? 0 : data.zoomVisiable;
        let iconFeature = new ol.Feature({
          geometry: new ol.geom.Point(local)
          // name_en: "<div style='width:100px;height:30px'><p><label>" + content + "</label></p></div>",
          // remark: "point"
        });
        map.getView().on('change:resolution', function () {
          if (map.getView().getZoom() < zoomVisiable) {
            layerVectorMark.setVisible(false);
          } else {
            layerVectorMark.setVisible(true);
          }
        })
        this.showMark = function () {
          let iconStyle = new ol.style.Style({
            image: new ol.style.Circle({
              radius: 10,
              fill: new ol.style.Fill({
                color: [34, 139, 34, 1]
              }),
              stroke: new ol.style.Stroke({
                color: [255, 215, 0, 1],
                width: 1
              })
            })
            // text: new ol.style.Text({
            //   font: '15px "Helvetica Neue", Arial, Helvetica, sans-serif',
            //   text: content.toString(),
            //   // backgroundFill: [255,215,0, 1],
            //   // stroke: new ol.style.Stroke({
            //   //     color: '#000',
            //   //     width: 1
            //   // }),
            //   fill: new ol.style.Fill({
            //     color: '#000'
            //   })
            // })
          });
          iconFeature.setStyle(iconStyle);
          markVectorRes.addFeature(iconFeature);
          map.removeLayer(layerVectorMark);
          map.addLayer(layerVectorMark);
        }
        this.addClick = function (callback) {
          iconFeature.set('remark', 'click');
          iconFeature.set('function', function () {
            callback();
          });
          // iconFeature.on('click', function () {
          //   callback();
          // })
        }
        this.getFeature = function () {
          return iconFeature;
        }
        this.getSource = function () {
          return markVectorRes;
        }
        this.delete = function () {
          markVectorRes.removeFeature(iconFeature);
          map.removeLayer(layerVectorMark);
          map.addLayer(layerVectorMark);
          // markInfo.close();
        }
        this.change = function (newImg) {
          iconFeature.setStyle(new ol.style.Style({
            image: new ol.style.Icon(/** @type {olx.style.IconOptions} */({
              anchor: [0.5, 46],
              anchorXUnits: 'fraction',
              anchorYUnits: 'pixels',
              src: 'MarkImage/' + newImg + '.png'
            }))
          }))
        }
      }
      return mark;
    })();
    // 添加点标注
    let Mark = (function () {
      var mark = function (data) {
        var markVectorRes = new ol.source.Vector();
        var layerVectorMark = new ol.layer.Vector({
          source: markVectorRes
        })
        var local = data.local;
        var style = data.style;
        var animateSwitch = data.animateSwitch;
        var zoomVisiable = data.zoomVisiable == undefined ? 0 : data.zoomVisiable;
        var iconFeature = new ol.Feature({
          geometry: new ol.geom.Point(local)
        });
        map.getView().on('change:resolution', function () {
          if (map.getView().getZoom() < zoomVisiable) {
            layerVectorMark.setVisible(false);
          } else {
            layerVectorMark.setVisible(true);
          }
        })
        this.showMark = function () {
          var iconStyle = new ol.style.Style({
            image: new ol.style.Icon(/** @type {olx.style.IconOptions} */({
              anchor: [0.5, 46],
              anchorXUnits: 'fraction',
              anchorYUnits: 'pixels',
              src: require('../image/Mark.png')
            }))
          });
          iconFeature.setStyle(iconStyle);
          markVectorRes.addFeature(iconFeature);
          map.removeLayer(layerVectorMark);
          map.addLayer(layerVectorMark);
          // map.getView().setCenter (local);
          var mapZoom = map.getView().getZoom() - 1;
          // if (animateSwitch != false) {
          //   map.getView().animate({
          //     zoom: mapZoom,
          //     duration: AnimationTime
          //   });
          //   setTimeout(() => {
          //     map.getView().animate({
          //       zoom: 18,
          //       center: local,
          //       duration: AnimationTime
          //     });
          //   }, AnimationTime)
          // }
        }
        this.addClick = function (callback) {
          iconFeature.set('remark', 'click');
          iconFeature.set('function', function () {
            callback();
          })
        }
        this.getFeature = function () {
          return iconFeature;
        }
        this.getSource = function () {
          return markVectorRes;
        }
        this.delete = function () {
          markVectorRes.removeFeature(iconFeature);
          map.removeLayer(layerVectorMark);
          map.addLayer(layerVectorMark);
        }
        this.change = function (newImg) {
          iconFeature.setStyle(new ol.style.Style({
            image: new ol.style.Icon(/** @type {olx.style.IconOptions} */({
              anchor: [0.5, 46],
              anchorXUnits: 'fraction',
              anchorYUnits: 'pixels',
              src: 'MarkImage/' + newImg + '.png'
            }))
          }))
        }
      }
      return mark;
    })();
    this.map = map;
    this.layerEN = layerEN;
    this.InfoWindow = InfoWindow;
    this.DrawPoint = DrawPoint;
    this.ManyMark = manyMark;
    this.Mark = Mark
  }
  // 投影直角坐标X,Y值转地理坐标经纬度
  projTransfromToLngAndLat (local) {
    return ol.proj.transform(local, 'EPSG:900913', 'EPSG:4326')
  }
  // 地理坐标经纬度转投影直角坐标X,Y值
  projTransfromToRightAngle (local) {
    return ol.proj.transform(local, 'EPSG:4326', 'EPSG:900913')
  }
}
export default Map;



