<template>
  <div class="block">
    <span class="demonstration">日期-范围：</span>
    <el-date-picker
      v-model="dateValue"
      type="daterange"
      value-format="yyyy-MM-dd"
      @change="emitDate"
      :picker-options="pickerOptions"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      align="left">
    </el-date-picker>
  </div>
</template>

<script>
  const preDate = new Date(new Date().getTime() - 30 * 24 * 60 * 60 * 1000);// 30天
  export default {
    data () {
      return {
        pickerOptions: {
          disabledDate (time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '最近一周',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('datePick', [start, end]);
            }
          },
          {
            text: '最近一个月',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('datePick', [start, end]);
            }
          },
          {
            text: '最近三个月',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('datePick', [start, end]);
            }
          }]
        },
        dateValue: [preDate, new Date()]
      };
    },
    created () {
      this.emitDate();
    },
    methods: {
      emitDate: function (){
        this.$emit('datePick', this.dateValue);
        this.$store.dispatch('setDate', this.dateValue);
      }
    }
  };
</script>
<style lang="stylus">

</style>




