<template>
  <div id="map"></div>
</template>

<script>
import ol from 'openlayers'
export default {
  mounted () {
    let map = new ol.Map({
      // 设置地图图层
      layers: [
        // 创建一个使用Open Street Map地图源的瓦片图层
        new ol.layer.Tile({source: new ol.source.OSM()})
      ],
      // 设置显示地图的视图
      view: new ol.View({
        center: [0, 0],    // 定义地图显示中心于经度0度，纬度0度处
        zoom: 2            // 并且定义地图显示层级为2
      }),
      // 让id为map的div作为地图的容器
      target: 'map'
    })
  }
}
</script>

<style >
#map{
  width: 100%;
  height: 100%;
}
</style>





