<style lang="scss">
  $bg-clolor: #0e3a5e;
  $font-color:#0e3a5e;
  .header-wrap{
    width:100%;
    height:70px;
    position: relative;
    z-index: 99999;
    background-color: $bg-clolor;
    .btn-nav-slide{
      position:fixed;
      top:18px;
      left:10px;
      width: 32px;
      display: flex;
      height: 32px;
      background-color: #fff;
      border-radius:5px;
      text-align: center;
      font-size: 25px;
      color:#888;
      justify-content: space-around;
      align-items: center;
      z-index:999999;
    }
    .nav-slide-wrap{
      width: 60px;
      position: fixed;
      z-index: 9999;
      left:0;
      top:0;
      height:100%;
      background-color: rgba(21,56,92,0.9);
      a{
        color:#fff;
      }
      overflow-y: auto;
      overflow-x: hidden;
      .nav-slide{
        position:relative;
        top: 58px;
        flex-flow: column wrap;
        .iconfont{
          font-size:36px !important;
        }
        li{
          text-align: center;
          margin-top:20px;
          &:hover label{
            visibility: visible;
          }
        }
        label{
          color:#fff;
          display: block;
          visibility: hidden;
        }
      }
    }
    .container{
      height:100%;
    }
    .el-header{
      width: 100%;
      height:70px;
      background-color: $bg-clolor;
      color: #FFF;
      .iconfont {
        font-size: 34px;
      }
      a{
        color: #FFF;
      }
      .el-row{
        height:70px;
      }
    }
  }
</style>
<template>
    <div class="header-wrap">
      <p class="btn-nav-slide hidden-md-and-up"  @click="openSlide($event)" >
        <icon name="bars"></icon>
      </p>
      <div class="nav-slide-wrap  hidden-md-and-up" v-if="isOpen" >
        <ul class="nav-slide">
          <li>
            <a href="javascript:"><i class="icon iconfont icon-shouye_shouye"></i></a>
            <label>首页</label>
          </li>
          <li>
            <a href="javascript:"><i class="icon iconfont icon-weibiaoti1"></i></a>
            <label>任务</label>
          </li>
          <li>
            <a href="javascript:"><i class="icon iconfont icon-loudongsaomiao"></i></a>
            <label>漏洞</label>
          </li>
          <li>
            <a href="javascript:"><i class="icon iconfont icon-tool"></i></a>
            <label>工具</label>
          </li>
          <li>
            <a href="javascript:"><i class="icon iconfont icon-wode"></i></a>
            <label>能力</label>
          </li>
          <li>
            <a href="javascript:"><i class="icon iconfont icon-92"></i></a>
            <label>退出</label>
          </li>
        </ul>
      </div class="btn-nav-slide hidden-md-and-up">
      <div class="container">
        <el-header>
          <el-row  type="flex" justify="center" align="middle">
            <el-col :xs="19" :sm="19" :md="12" :lg="12" :xl="12">
              <AbInputSelSearch></AbInputSelSearch>
            </el-col>
            <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12" class="hidden-sm-and-down">
              <AbNavBarIcons></AbNavBarIcons>
            </el-col>
          </el-row>
        </el-header>
      </div>
    </div>
</template>
<script type="text/ecmascript-6">
  import AbInputSelSearch from '../views/App/ability/common/AbInputSelSearch.vue'
  import AbNavBarIcons from '../views/App/ability/common/AbNavBarIcons.vue'
  export default {
    data () {
      return {
        page: "header",
        keyword: '',
        select: ''
      }
    },
    computed: {
      isOpen () {
        return this.$store.getters.slideState
      }
    },
    methods: {
      openSlide ($event){
        $event.stopPropagation();
        this.$store.dispatch('setSlideState')
      }
    },
    components: {
      AbInputSelSearch,
      AbNavBarIcons
    }
  }
</script>





