				  </ul>
				</el-col>
				<el-col :xs="24" :sm="16" :md="16" :lg="16" :xl="16">
					<div id="maps" style="height: 510px" class="bg-blue">
            <HeatMap></HeatMap>
					</div>
				</el-col>
			</el-row>
