				</el-col>
				<el-col :span="15" :xs="24" :sm="24" :md="16" :lg="16" :xl="16"  style="padding-left: 7px;">
					<el-row :gutter="20" style="height: auto;" class="clearfix  bg-blue">
						  <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
