<script type="text/ecmascript-6">
  import echarts from 'echarts/lib/echarts';
  import HeatMap from '../../../../common/HeatMap'
  export default {
    data: function () {
      return {
        msg: "coverage",
        keyword: "",
        tableData: [
          {
            ip: '163.128.6.2',
            url: 'Linux Kernel 4.4.0 (Ubuntu) - DCCP Double-Free (PoC)',
            browsers: 'Mozilla/5.0',
            date: '2017-03-08',
            operasys: 'Windows NT 6.1;WOW64'
          },
          {
            ip: '163.128.6.2',
            url: 'Linux Kernel 4.4.0 (Ubuntu) - DCCP Double-Free (PoC)',
            browsers: 'Mozilla/5.0',
            date: '2017-03-08',
            operasys: 'Windows NT 6.1;WOW64'
          }
        ],
        chartPie: null,
        chartBar: null,
        chartBarIp: null
      }
    },
    mounted () {
      let objChartPie = document.getElementById('chartPie');
      let objChartBar = document.getElementById('chartBar');
      let objChartBarIp = document.getElementById('chartBarIp');
      this.chartPie = echarts.init(objChartPie);
      this.chartBar = echarts.init(objChartBar);
      this.chartBarIp = echarts.init(objChartBarIp);
      this.getChartData();
      let self = this;
      window.addEventListener("resize", function () {
        self.chartPie.resize();
        self.chartBar.resize();
        self.chartBarIp.resize();
      });
    },
    methods: {
      getChartData (data) {
        this.chartPie.setOption(this.setOptionPie());
        this.chartBar.setOption(this.setOptionBar());
        this.chartBarIp.setOption(this.setOptionBarIp());
      },
      setOptionPie (data){
        return {
          legend: {
            orient: 'horizontal',
            x: '5%',
            y: '4%',
            data: ['netFlow', 'dns', 'log', 'http head'],
            textStyle: {color: "#9ea3ab"} // 文字颜色
          },
          calculable: false,  // 饼图外围圈
          series: [
            {
              name: '访问来源',
              type: 'pie',
              radius: '60%',
              center: ['50%', '60%'],
              data: [
                {value: 335, name: 'netFlow'},
                {value: 310, name: 'dns'},
                {value: 234, name: 'log'},
                {value: 135, name: 'http head'}
              ],
              label: {  // 指示线上的文字标签
                normal: {
                  show: true
                }
              },
              labelLine: {   // 指示线显示
                normal: {
                  show: true
                }
              },
              color: ['#c23630', '#2f4653', '#61a0a8', '#d48265']
            }
          ]
        };
      },
      setOptionBar (data) {
        return {
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['域名', '系统', '浏览器'],
            top: '4%',
            textStyle: {color: "#9ea3ab"} // 文字颜色
          },
          calculable: true,
          xAxis: [
            {
              position: 'top',
              type: 'value',
              boundaryGap: [0, 0.01],
              // 设置坐标轴字体颜色和宽度
              axisLine: {
                lineStyle: {
                  color: '#9ea3ab',
                  width: 1
                }
              }
            }
          ],
          yAxis: [
            {
              type: 'category',
              data: ['renren', 'sina', 'facebook', 'yahoo', 'baidu', 'google'],
              axisLine: {
                lineStyle: {
                  color: '#9ea3ab',
                  width: 1
                }
              }
            }
          ],
          grid: {
            left: '0%',
            right: '4%',
            bottom: '5%',
            containLabel: true
          },
          series: [
            {
              itemStyle: { // 条形图颜色更改
                normal: {color: '#1158a9'}
              },
              name: '域名',
              type: 'bar',
              data: [18203, 23489, 29034, 104970, 131744, 630230]
            },
            {
              itemStyle: {
                normal: {color: '#61a0a8'}
              },
              name: '系统',
              type: 'bar',
              data: [19325, 23438, 31000, 121594, 134141, 681807]
            },
            {
              itemStyle: {
                normal: {color: '#c23531'}
              },
              name: '浏览器',
              type: 'bar',
              data: [19325, 23438, 31000, 121594, 134141, 681807]
            }
          ]
        };
      },
      setOptionBarIp (data) {
        return {
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['IP排行'],
            top: '4%',
            textStyle: {color: "#9ea3ab"} // 文字颜色
          },

          calculable: true,
          xAxis: [
            {
              position: 'top',
              type: 'value',
              boundaryGap: [0, 0.01],
              // 设置坐标轴字体颜色和宽度
              axisLine: {
                lineStyle: {
                  color: '#9ea3ab',
                  width: 1
                }
              }
            }
          ],
          yAxis: [
            {
              type: 'category',
              data: ['renren', 'sina', 'facebook', 'yahoo', 'baidu', 'google'],
              // 设置坐标轴字体颜色和宽度
              axisLine: {
                lineStyle: {
                  color: '#9ea3ab',
                  width: 1
                }
              }
            }
          ],
          grid: {
            left: '0%',
            right: '4%',
            bottom: '5%',
            containLabel: true
          },
          series: [
            {
              itemStyle: {
                normal: {color: '#d68563'}
              },
              name: 'IP排行',
              type: 'bar',
              data: [18203, 23489, 29034, 104970, 131744, 630230]
            }
          ]
        };
      }
    },
    components: {
      HeatMap
    }
  }
</script>



