<template>
  <el-input v-model.trim="keywords" :chanage="search()" placeholder="请输入查询关键字"></el-input>
</template>

<script type="text/ecmascript-6">
  import debounce from 'debounce';
  const search = {
    data () {
      return {
        keywords: ''
      }
    },
    methods: {
      search: debounce(function () {
        this.$emit('query', this.keywords)
      }, 900)
    }
  }
  export default search
</script>
<style lang="stylus">

</style>




