<style lang="scss" scoped>
  #ability-main {
    >.container{
      padding: 0;
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding-top: 70px;
      .el-row{
        height:100%;
      }
      .el-col{
        height:100%;
      }
    }
  }
@media (max-width: 1200px) {
   #ability-main>.container {
    padding: 0;
    position: relative;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
}
  .content{
    width: 100%;
    height: 100%;
    padding: 10px;
  }
  #asnforce{
    height:50%;
    overflow: hidden;
  }
  #asnmap{
    height:100%;
    padding-right: 8px;
  }
  #asnpoint{
    height:50%;
    padding-top: 8px;
    position: relative;
  }
</style>
<template>
  <div class="container">
    <!-- <div class="content-wrap"> -->
      <div class="content">
        <el-row>
          <el-col :xs="24" :sm="24" :lg="14" :xl="14">
            <div id="asnmap">
              <mapSearch></mapSearch>  
              <AsnMap></AsnMap>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :lg="10" :xl="10">
            <div id="asnforce">
              <AsnForce></AsnForce>
            </div>
            <div id="asnpoint">
              <AsnPoint></AsnPoint>
            </div>
          </el-col>
        </el-row>
      </div>
    <!-- </div  > -->
  </div>
</template>
<script>
import AsnForce from './AsnForce.vue';
import AsnMap from './AsnMap.vue';
import AsnPoint from './AsnPoint.vue';
import mapSearch from './mapSearch.vue';
export default {
  data () {
    return {
      page: "controls"
    }
  },
  components: {
    AsnForce,
    AsnMap,
    AsnPoint,
    mapSearch
  }
}
</script>




