<style lang="scss">
  #ability-bg {
    height: 100%;
    overflow: auto;
    position: relative;
    background: #141f31 url('../../../../assets/image/bg.png') center top no-repeat;
  }
  // #ability-main{
  // }
  /*放射线*/
  #line1,#line2,#line3{
    width:5px;
    height:5px;
    background:#162a42;
    border-radius: 4px;
    position:absolute;
    top:130px;
    right:11%;
    animation:myfirst 4s linear 0.5s infinite ;
    /* Firefox: */
    -moz-animation:myfirst 4s linear 0.5s infinite ;
    /* Safari and Chrome: */
    -webkit-animation:myfirst 4s linear 0.5s infinite ;
    /* Opera: */
    -o-animation:myfirst 4s linear 0.5s infinite ;
  }

  @keyframes myfirst
  {
    0%   {background: linear-gradient(to left, rgba(46,159,245,0), #27a2f3);}
    25%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:140px; }
    50%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:250px; }
    75%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:360px;}
    100% {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:480px; }
  }
  @-moz-keyframes myfirst /* Firefox */
  {
    0%   {background: linear-gradient(to left, rgba(46,159,245,0), #27a2f3);}
    25%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:140px; }
    50%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:250px; }
    75%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:360px;}
    100% {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:480px; }
  }

  @-webkit-keyframes myfirst /* Safari and Chrome */
  {
    0%   {background: linear-gradient(to left, rgba(46,159,245,0), #27a2f3);}
    25%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:140px; }
    50%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:250px; }
    75%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:360px;}
    100% {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:480px; }
  }

  @-o-keyframes myfirst /* Opera */
  {
    0%   {background: linear-gradient(to left, rgba(46,159,245,0), #27a2f3);}
    25%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:140px; }
    50%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:250px; }
    75%  {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:360px;}
    100% {background:linear-gradient(to left, rgba(46,159,245,0), #27a2f3);width:480px; }
  }
  #line2{
    top:170px;
    right:19%;
    animation:myfirst 5s linear 0.5s infinite ;
    /* Firefox: */
    -moz-animation:myfirst 5s linear 0.5s infinite ;
    /* Safari and Chrome: */
    -webkit-animation:myfirst 5s linear 0.5s infinite ;
    /* Opera: */
    -o-animation:myfirst 5s linear 0.5s infinite ;
  }

  #line1>span,#line2>span{
    display:inline-block;
    position: absolute;
    top:-2px;
    left:-6px;
    color:#27a2f3;
    width: 8px;
    height:8px;
    background:#27a2f3 ;
    border-color:rgba(39,162,243,.75);
    border-radius: 50%;
    transition:border linear .2s,-webkit-box-shadow linear .5s;
    -webkit-transition:border linear .2s,-webkit-box-shadow linear .5s;
    -moz-transition:border linear .2s,-webkit-box-shadow linear .5s;
    -o-transition:border linear .2s,-webkit-box-shadow linear .5s;
    box-shadow:0 0 25px rgba(255,255,255,1);
    -webkit-box-shadow:0 0 25px rgba(255,255,255,1);
    -moz-box-shadow:0 0 25px rgba(255,255,255,3);
    -o-box-shadow:0 0 25px rgba(255,255,255,3);
  }


  #case{
    width:200px;
    height:100px;
    overflow: hidden;
    position: absolute;
    left:8%;
    top:110px;
    color: #1b74aa;
    font-size: 12px;
  }
  .tadpole>li{
    font-size: 12px;
    line-height: 12px;
    color: #1b74aa;
  }
  .tadpole{
    font-size: 12px;
    position:absolute;
    top:0;
    animation:tadpole 4s linear 0.5s infinite both;
    /* Firefox: */
    -moz-animation:tadpole 4s linear 0.5s infinite both;
    /* Safari and Chrome: */
    -webkit-animation:tadpole 4s linear 0.5s infinite both;
    /* Opera: */
    -o-animation:tadpole 4s linear 0.5s infinite both;
  }

  @keyframes tadpole{
    from  {top:0;}
    to {top:-100%;}
  }
  @-moz-keyframes tadpole /* Firefox */
  {
    from  {top:0;}
    to {top:-100%;}
  }
  @-webkit-keyframes tadpole /* Safari and Chrome */
  {
    from  {top:0;}
    to {top:-100%;}
  }
  @-o-keyframes tadpole /* Opera */
  {
    from{top:0;}
    to {top:-100%;}
  }
</style>
<template>
  <div id="ability-bg">
    <vHeader></vHeader>
    <div id="ability-main">
      <div id="line1"><span></span></div>
      <div id="line2"><span></span></div>
      <div id="case">
        <ul class="tadpole">
          <li> งพปู่เจ้าส....มิงพราย ปู่เจ้าสมิงพราย ปู่เจ้าสมิงพราย</li>
          <li> งพปู่เจ้าสมิงพราย ปู่เจ้าสมิงพราย  ปู่เจ้าสมิงพราย </li>
          <li> งพปู่เจ..</li>
          <li> มิงพรปู่เจ้าสายมิงพร......</li>
          <li>พราย ปู่เจ้าส....มิงาสมิง</li>
          <li>าสมิง...มิงพร</li>
          <li>ปู่เจ้าสมิงพรปู่เจ้พรปู่เจ้พรปู่เจ้พรปู่เจ้พรปู่เจ้</li>
          <li> งพปู่เจ้าสมิงพราย ปู่เจ้าสมิงพราย  ปู่เจ้าสมิงพราย </li>
          <li> งพปู่เจ..</li>
          <li> มิงพรปู่เจ้าสายมิงพร......</li>
          <li>พราย ปู่เจ้าส....มิงาสมิง</li>
        </ul>
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        page: "index"
      }
    },
    mounted () {
      // this.slideOff();
    },
    methods: {
      slideOff: function (){
        let $html = document.documentElement;
        let vm = this;
        $html.onclick = function (e){
          vm.$store.dispatch('closeSlide', false)
        }
      }
    }
  }
</script>



