import ol from 'openlayers'
let markArray = []
let childArray = []

class PointLine{
  constructor (mapshow, dt) {
    let source = new ol.source.Vector({
      wrapX: false
    });
    for (let i = 0; i < dt.length - 1; i++) {
      let features = new ol.Feature({
        geometry: new ol.geom.LineString([mapshow.projTransfromToRightAngle(dt[i].coord), mapshow.projTransfromToRightAngle(dt[i + 1].coord)])
      });
      source.addFeature(features);
      features.setStyle(new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: 3,
          color: 'rgba(34, 139, 34, 0.7)'
        })
      }));
    }
    let layer = new ol.layer.Vector({
      source: source
    });
    let addPoint = function (jsonData) {
      let pointArr = jsonData;
      let ass;
      pointArr.forEach(function (e) {
        let data = {
          local: mapshow.projTransfromToRightAngle(e.coord),
          style: 'webshell',
          offset: [0.5, 15]
          // content: element.id
        }
        ass = new mapshow.ManyMark(data);
        ass.showMark();
        ass.addClick(function () {
          let content = '<p class="tip_p" >' + e.ip + '<br/>设备类型：' + e.设备类型 + '<br/>CPU占用率：' + e.CPU占用率 + '<br/>设备型号：' + e.型号 + '>';
          var info = new mapshow.InfoWindow({
            offset: [15, -5],
            closeVisiable: true,
            content: content
          });
          info.close();
          info.open(data.local, null, content);
          console.log(5555555555555555)
          // var arr = e.children;
          // arr.forEach((element) => {
          //   let childData = {
          //     local: mapshow.projTransfromToRightAngle(element.coord),
          //     style: 'green',
          //     offset: [0.5, 15]
          //   }
          //   let child = new mapshow.Mark(childData);
          //   child.showMark();
          //   child.addClick(function () {
          //     console.log('child')
          //   })
          // })
        })
        markArray.push(ass);
      })
    }
    var addchild = function (data) {
      var arr = data.children;
      if (arr) {
        arr.forEach((e) => {
          let childData = {
            local: mapshow.projTransfromToRightAngle(e.coord),
            style: 'green',
            offset: [0.5, 15]
          }
          let child = new mapshow.Mark(childData);
          child.showMark();
          child.addClick(function () {
            let content = '<p class="tip_p" >' + e.ip + '<br/>设备类型：' + e.设备类型 + '<br/>CPU占用率：' + e.CPU占用率 + '<br/>设备型号：' + e.型号 + '>';
            var info = new mapshow.InfoWindow({
              offset: [12, -40],
              closeVisiable: true,
              content: content
            });
            info.close();
            info.open(childData.local, null, content);
            console.log('child')
          })
          childArray.push(child);
        })
      }
    }
    this.showPoint = function (jsonData) {
      addPoint(jsonData)
    }
    this.showChild = function (jsonData) {
      // addChild(jsonData)
      console.log(jsonData)
      let arr = jsonData
      arr.forEach((e) => {
        addchild(e)
      })
    }
    this.showLine = function () {
      mapshow.map.addLayer(layer);
    }
    this.removeLine = function () {
      mapshow.map.removeLayer(layer);
    }
    this.close = function () {
      markArray.forEach(function (e) {
        e.delete();
      })
      markArray.length = 0;
      if (info) {
        info.close();
      }
      if (time) {
        clearInterval(time);
      }
    }
    this.closeChild = function () {
      childArray.forEach(function (e) {
        e.delete();
      })
      childArray.length = 0;
      // if (info) {
      //   info.close();
      // }
      // if (time) {
      //   clearInterval(time);
      // }
    }
  }
}
export default PointLine



