const commonFn = {
  logout (){
    // 发起ajax请求退出成功后清除localStorage
    localStorage.removeItem('userInfo');
    router.push('/login');
  },
  navActived (curl){
    if (curl === this.$route.path){
      return true;
    }
  },
  simpleFormatDate (timeStr){
    if (Object.prototype.toString.call(timeStr) === '[object Date]'){
      return timeStr.getFullYear() + '-' + this.addZero(timeStr.getMonth() + 1) + '-' + this.addZero(timeStr.getDate())
    } else {
      return timeStr;
    }
  },
  addZero (number) {
    let num = Number(number);
    if (num && num < 10){
      num = '0' + num;
    }
    return num;
  },
  filterData (datas) {
    datas.forEach(function (v) {
      v['role'] === 0 ? v['role'] = '普通用户' : v['role'] = "管理员"
      switch (v['parent']){
        case 1 :
          v['parent'] = '诊断工具';
          break;
        case 2 :
          v['parent'] = '慧眼平台';
          break;
        case 3 :
          v['parent'] = '收益数据分析';
          break;
      }
    });
    return datas;
  },
  setChartHeight (parentId, childId) {
    document.getElementById(childId).style.height = document.getElementById(parentId).offsetHeight + 'px';
  }
}

export default commonFn



