const Host = process.env.NODE_ENV === 'development' ? 'api_test' : 'api'
const Api = {
  aside: '/demo/aside',
  login: '/demo/login',
  reportsAdd: '/demo/reports-add',
  reportsList: '/demo/reports-list',
  reportsDel: '/demo/reports-del'
};
function getApi (str) {
  var apiStr = str ? 'http://localhost:9090' + Api[str] : '';
  return apiStr
}
export default getApi



