<style lang="scss">
  #map{
    width: 100%;
    height: 100%;
  }
  .el-checkbox-group{
    position: absolute;
    top: 20px;
    left: 20px;
    .el-checkbox{
      color:#a0a0a0;
    }
  }
  @keyframes warn2 {
    0% {
      transform: scale(0.3);
      -webkit-transform: scale(0.3);
      opacity: 1;
    }
    25% {
      transform: scale(0.5);
      -webkit-transform: scale(0.5);
      opacity: 0.8;
    }
    50% {
      transform: scale(0.6);
      -webkit-transform: scale(0.6);
      opacity: 0.6;
    }
    75% {
      transform: scale(0.8);
      -webkit-transform: scale(0.8);
      opacity: 0.2;
    }
    100% {
      transform: scale(1);
      -webkit-transform: scale(1);
      opacity: 0.0;
    }
  }

  #gifMark {
    position: relative;
    width: 40px;
    height: 40px;
  }
  .dot {
    position: absolute;
    width: 15px;
    height: 15px;
    left: 15px;
    top: 15px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    z-index: 2;
  }
  .pulse {
    position: absolute;
    width: 40px;
    height: 40px;
    left: 2px;
    top: 2px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    z-index: 1;
    opacity: 1;
    -webkit-animation: warn2 2s ease-out;
    -moz-animation: warn2 2s ease-out;
    animation: warn2 2s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
  }
  .pulse1 {
    position: absolute;
    width: 40px;
    height: 40px;
    left: 2px;
    top: 2px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    z-index: 1;
    opacity: 1;
    -webkit-animation: warn2 3s ease-out;
    -moz-animation: warn2 3s ease-out;
    animation: warn2 3s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
  }

  .pulse2 {
    position: absolute;
    width: 40px;
    height: 40px;
    left: 2px;
    top: 2px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    z-index: 1;
    opacity: 1;
    -webkit-animation: warn2 4s ease-out;
    -moz-animation: warn2 4s ease-out;
    animation: warn2 4s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    /*box-shadow: 1px 1px 30px #0ff;*/
  }
  /* 信息窗体样式*/
  .ol-popup {
    position: absolute;
    background-color: white;
    -webkit-filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
    filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
    /*padding: 15px;*/
    border-radius: 10px;
    border: 1px solid #cccccc;
    bottom: 5px;
    left: -50px;
    max-width: 500px;
    min-width: 60px;
    white-space: nowrap;
    word-wrap: break-word;
    z-index: 3;
  }
  .ol-popup:after,
  .ol-popup:before {
    top: 100%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
  }

  .ol-popup:after {
    border-top-color: #fff;
    border-width: 10px;
    left: 35px;
    margin-left: -10px;
  }

  .ol-popup:before {
    border-top-color: rgba(3, 29, 86, 0.7);
    border-width: 11px;
    left: 35px;
    margin-left: -11px;
  }

  .ol-popup-closer {
    text-decoration: none;
    position: absolute;
    top: 0px;
    right: 8px;
    cursor: pointer;
  }
  .ol-popup-closer:after {
    content: "✖";
  }

  .ol-popup-title {
    padding: 8px 14px;
    margin: 0;
    background-color: #f8f8f8;
    border-bottom: 1px solid #ebebeb;
    border-radius: 5px 5px 0 0;
  }

  .ol-popup-content {
    padding: 6px 12px;
    /*padding-bottom: 0px;*/
  }
  #asnpointWrap{
    width: 100%;
    height: 100%;
    position: relative;
  }
</style>
<template>
  <div id="asnpointWrap">
    <div id="map"></div>
    <el-checkbox-group v-model="checkedList" @change="handleListChange">
      <el-checkbox v-for="list in checklists" :label="list" :key="list"></el-checkbox>
    </el-checkbox-group>
  </div>
</template>
<script type="text/ecmascript-6">
import Ipload from '../../../../../assets/js/Ipload';
import Map from '../../../../../assets/js/map';
const checked = ['webshell', '运营商', '设备节点'];
let pointlist;
export default {
  data () {
    return {
      // page: "Point"
      checklists: ['webshell', '运营商', '设备节点'],
      checkedList: checked,
      showMap: new Map(),
      Iplist: null
    }
  },
  methods: {
    getPoint () {
      axios.get('../../../../../../static/libs/pointData.json').then((res) => {
        pointlist = res.data;
        this.showList(pointlist.yys)
        this.showList(pointlist.webshell)
        this.showList(pointlist.device)
      })
    },
    showList (list) {
      this.Iplist = new Ipload(this.showMap, list);
      this.Iplist.show(list)
    },
    closeList (list) {
      this.Iplist = new Ipload(this.showMap, list);
      this.Iplist.close(list)
    },
    handleListChange (value) {
      this.closeList(pointlist.yys)
      this.closeList(pointlist.webshell)
      this.closeList(pointlist.device)
      value.forEach((element) => {
        if (element === '运营商') {
          this.showList(pointlist.yys)
        } else if (element === 'webshell') {
          this.showList(pointlist.webshell)
        } else if (element === '设备节点') {
          this.showList(pointlist.device)
        }
      });
    }
  },
  mounted () {
    _g.setChartHeight('asnpointWrap', 'map')
    this.showMap.map.setTarget('map')
    this.getPoint()
  }
}
</script>



