import * as types from '../mutation-types'
const state = {
  curDate: {}
}

// getters
const getters = {
  getDate: state => state.curDate
}

// actions
const actions = {
  setDate ({ commit, state }, curDate) {
    commit(types.LIST_REPORTS, { curDate })
  }
}

// mutations
const mutations = {
  [types.LIST_REPORTS] (state, { curDate }) {
    var tpmdate = {
      'minDate': _g.simpleFormatDate(curDate[0]),
      'maxDate': _g.simpleFormatDate(curDate[1])
    }
    state.curDate = tpmdate
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



