<template>
  <el-main>
    <Search @query="query" style="margin-bottom:20px;width: 300px;position: relative;left:50%;transform:translateX(-50%)"></Search>
    <el-table :data="tableData" stripe>
      <el-table-column fixed prop="rname" label="报表名称" width="230">
    </el-table-column>
      <el-table-column prop="parent" label="报表目录" width="150">
      </el-table-column>
      <el-table-column prop="curl" label="报表地址">
      </el-table-column>
      <el-table-column prop="role" label="报表角色">
      </el-table-column>
      <el-table-column prop="address" label=" " width="60">
        <template slot-scope="scope">
          <el-button @click="del(scope)" type="text" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align:center;position: absolute;bottom:20px;left:40%;">
      <el-pagination
        layout="prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
  </el-main>

</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http'
  import { mapGetters } from 'vuex'
  export default {
    data () {
      return {
        tableData: []
      }
    },
    watch: {
      '$route': 'fetchData'
    },
    created () {
      let apiStr = Api('reportsList');
      this.fetchData(apiStr)
    },
    methods: {
      fetchData: function (apiStr){
        this.apiGet(apiStr).then(
          res => {
            this.tableData = _g.filterData(res);
          },
          err => {
            console.log(err);
          }
        )
      },
      query: function (data) {
        let ApiStr = Api('reportsList') + "?" + 'keywords=' + data;
        this.fetchData(ApiStr)
      },
      del: function (scope) {
        const rid = scope.row.id;
        this.$http.delete(Api('reportsDel'), {body: {id: rid}}).then(
          res => {
            for (let i = 0; i < this.tableData.length; i++) {
              var cur = this.tableData[i];
              if (Number(cur.id) === Number(rid)) {
                this.tableData.splice(i, 1);
              }
            }
          },
          err => {
            console.log(err);
          }
        )
      }
    },
    mixins: [http]
  }
</script>
<style lang="stylus">

</style>




