import ol from 'openlayers';
// import DrawPoint from './drawPoint'
let markArray = [];
let info;
let time;
let IP;
class IPload {
  constructor (mapshow, dt) {
    // let mapshow = new Map()
    let addPoint = function (jsonData) {
      // 获得Json数据的字段名称
      let property = [];
      for (let p in jsonData) {
        property.push(p);
      }
      IP = jsonData[property[0]];
      let mk
      IP.forEach(function (e) {
        let longitude = e.经度;
        let latitude = e.纬度;
        let style = e.设备类型;
        let data = {
          local: ol.proj.transform([longitude, latitude], 'EPSG:4326', 'EPSG:900913'),
          style: style,
          // shebei:'标识',
          offset: [-20, -20]
        }
        mk = new mapshow.DrawPoint(data);
        mk.showImgMark();
        mk.addClick(function () {
          let content = '<p class="tip_p" >' + e.ip + '<br/>设备类型：' + e.设备类型 + '<br/>CPU占用率：' + e.CPU占用率 + '<br/>设备型号：' + e.型号 + '>';
          info = new mapshow.InfoWindow({
            offset: [20, 0],
            closeVisiable: true,
            content: content
          });
          info.close();
          info.open(ol.proj.transform([longitude, latitude], 'EPSG:4326', 'EPSG:900913'), null, content);
        })
        markArray.push(mk);
      })
    }
    this.show = function (jsonData) {
      addPoint(jsonData.IP)
    }
    this.close = function () {
      markArray.forEach(function (e) {
        e.delete();
      })
      markArray.length = 0;
      if (info) {
        info.close();
      }
      if (time) {
        clearInterval(time);
      }
    }
  }
}
export default IPload;



