// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import debounce from 'debounce';
import axios from 'axios';
import Lockr from 'Lockr';
import Cookies from 'js-cookie';
import lodash from 'lodash';
import moment from 'moment';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import 'element-ui/lib/theme-chalk/display.css';
import VueRouter from 'vue-router';
import routes from './routes';
import store from './vuex';
import vueResource from 'vue-resource';
import 'vue-awesome/icons';
import Icons from 'vue-awesome/components/Icon';
import echarts from 'echarts';
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';
import './assets/css/reset.css';
import './assets/scss/media.scss';
import './assets/fonts/iconfont.css';

/* 全局组件 */
import App from './App';
import Home from './components/views/App/Home';
import Login from './components/common/Login';
import dataPickerRange from './components/common/dataPickerRange';
import Search from './components/common/Search';
import vHeader from './components/common/VHeader';

/* 全局方法 */
import filter from './assets/js/filter.js';
import http from './assets/js/http.js';
import _global from './assets/js/global.js';
import Api from './assets/js/Api.js';
Vue.config.productionTip = false;

/* 注册插件 */
Vue.use(VueRouter);
Vue.use(vueResource);
Vue.use(debounce);
Vue.use(ElementUI, { size: 'small' });
Vue.use(echarts);

// // 设置axios头信息
// axios.default.timeout = 1000 * 15;
// axios.default.headers.authkey = '' || Lockr.get('authkey');
// axios.default.headers.sessionId = '' || Lockr.get('sessionId');
// axios.default.headers['Content-Type'] = 'application/json';
// axios.intercepter.request.use(function(config){
//   if (store.state.authkey){
//     config.headers.authkey = Lockr.get('authkey')
//   }
//   return config;
// },function (err) {
//   return Promise.reject(err);
// });

// 注册路由并设置路由守卫
const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes,
  linkActiveClass: 'active'
})
router.beforeEach((to, from, next) => {
  NProgress.start();
  next();
  // const userInfo = localStorage.getItem('userInfo');
  // if (to.path === '/login'){
  //   next();
  // } else {
  //   if (!userInfo) {
  //     next({path: '/login'})
  //   } else {
  //     next();
  //   }
  // }
})
router.afterEach(transtion => {
  NProgress.done();
})

// 设置公共方法
window.router = router;
window.store = store;
window.axios = axios;
window._debounce = debounce;
window._lodash = lodash;
window._g = _global;
window.moment = moment;
window.Cookies = Cookies;
window.Api = Api;
// 注册全局组件
Vue.component('icon', Icons);
Vue.component('Login', Login);
Vue.component('dataPickerRange', dataPickerRange);
Vue.component('Search', Search);
Vue.component('vHeader', vHeader);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  filters: filter,
  router,
  store,
  template: '<App/>',
  debounce: debounce,
  components: { App }
});



