<template>
  <ul id="ab-navbar-icons" class=" flex-row-end">
    <li>
      <a href="javascript:"><i class="icon iconfont icon-shouye"></i></a>
      <label>首页</label>
    </li>
    <li>
      <a href="javascript:"><i class="icon iconfont icon-loudongsaomiao"></i></a>
      <label>漏洞</label>
    </li>
    <li>
      <a href="javascript:"><i class="icon iconfont icon-tool"></i></a>
      <label>工具</label>
    </li>
    <li>
      <a href="javascript:"><i class="icon iconfont icon-wode"></i></a>
      <label>能力</label>
    </li>
    <li>
      <a href="javascript:"><i class="icon iconfont icon-92"></i></a>
      <label>退出</label>
    </li>
  </ul>
</template>
<style lang="scss">
  #ab-navbar-icons{
    width: 100%;
    height: 70px;
    .iconfont {
      font-size: 34px;
    }
    li{
      float:left;
      position: relative;
      margin-left:20px;
      &:nth-last-of-type(2){
        .iconfont {
          font-size: 52px;
        }
        label{
          top: 54px;
          right:10px;
        }
      }
      label{
        font-size:14px;
        display: none;
        position: absolute;
        right: 3px;
        top: 40px;

      }
      &:hover{
        label{
          display: block;
        }
      }
      &:last-child{
        margin-left:40px;
      }
    }
  }
</style>
<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        page: 'this'
      }
    }
  }
</script>



