<style lang='scss'>
  #searchMap{
    position: absolute;
    left: 15px;
    top: 15px;
    z-index: 2;
    >input{
        float:left;
        width: 260px;
        height: 40px ;
        line-height: 40px;
        border-radius: 0;
        border: 0;
        padding:0 10px;
        color: #0c1a2c;
        font-size: 14px;
    }
    .searchIcon{
      float: left;  
      height: 40px;
      line-height: 46px;
      width: 40px;
      background-color: #fff;
      cursor: pointer;
      text-align: center;
      color: #0c1a2c;
    }
  }
</style>
<template>
  <div id="searchMap">
    <input type="text" v-model="keywords" placeholder="请输入IP、机构、ASN号进行查询">
    <span class="searchIcon" @click="search()">
      <icon name="search"></icon>
    </span>
  </div> 
</template>
<script type="text/ecmascript-6">
    import {mapGetters} from 'vuex';
    export default {
      data () {
        return {
          keywords: ''
        };
      },
      computed: {
      },
      methods: {
        search: function () {
          this.$store.dispatch('changeAbilityKeyword', this.keywords)
        }
      },
      mounted () {
      }
    }
</script>



