<template>
  <div id="root-div">
    <VHeader></VHeader>
    <el-container>
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu router :default-active="activeRouter">
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-message"></i>报表管理</template>
            <el-menu-item-group>
              <template slot="title">报表管理</template>
              <el-menu-item index="/manage/list">报表列表</el-menu-item>
              <el-menu-item index="/manage/add">增加报表</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group>
              <template slot="title">报表目录管理</template>
              <el-menu-item index="1-3">目录列表</el-menu-item>
              <el-menu-item index="1-4">增加目录</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-menu"></i>用户管理</template>
            <el-menu-item-group>
              <template slot="title">用户管理</template>
              <el-menu-item index="2-1">用户列表</el-menu-item>
              <el-menu-item index="2-2">增加用户</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="分组2">
              <template slot="title">角色管理</template>
              <el-menu-item index="2-3">角色列表</el-menu-item>
              <el-menu-item index="2-4">增加角色</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title"><i class="el-icon-setting"></i>权限管理</template>
            <el-menu-item-group>
              <template slot="title">权限管理</template>
              <el-menu-item index="3-1">权限管理</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <transition name="fade" mode="out-in" :duration="100" appear>
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </transition>
      </el-main>
    </el-container>
  </div>
</template>

<script type="text/ecmascript-6">
   export default {
     data () {
       return {
         activeRouter: this.$route.path
       }
     }
   }
</script>
<style lang="stylus">
  #root-div
    height:100%
    >.el-container
      height:100%
  .el-aside
    color: #333
  .router-link{
    display:block;
    width: 100%;
    height: 100%;
  }

</style>



