import Home from './components/views/App/Home'
import Ability from './components/views/App/ability/'
import Recognise from './components/views/App/ability/recognise/Recognise'
import Coverage from './components/views/App/ability/coverage/Coverage'
import Controls from './components/views/App/ability/controls/Controls'

import Manage from './components/views/manage/Manage'
import ManageReports from './components/views/manage/ManageReports'
import Login from './components/common/Login'

const routes = [
  {
    path: '/index',
    name: 'index',
    component: Home,
    redirect: '/index/ability/coverage',
    children: [
      {
        path: 'ability',
        name: 'ability',
        component: Ability,
        children: [
          {
            path: 'recognise',
            name: 'recognise',
            component: Recognise
          },
          {
            path: 'coverage',
            name: 'coverage',
            component: Coverage
          },
          {
            path: 'controls',
            name: 'controls',
            component: Controls
          }
        ]
      }
    ]
  },
  {
    path: '/manage',
    name: 'manage',
    component: Manage,
    redirect: '/manage/list',
    children: [
      {
        path: 'list',
        name: 'list',
        component: ManageReports
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/',
    redirect: '/index/'
  }
];
export default routes



