<style lang="scss">
#lineMap{
  width: 100%;
}
</style>
<template>
    <div id="lineMap"></div>
</template>
<script type="text/ecmascript-6">
import Map from '../../../../../assets/js/map'
import PointLine from '../../../../../assets/js/pointLine'
export default {
  data () {
    return {
      // page: "Map"
      showMap: new Map(),
      pointLinelist: null,
      pointline: null
    }
  },
  methods: {
    async getDataPoint () {
      let res = await axios.get("../../../../../../static/libs/pointLine.json")
      this.pointLinelist = res.data.ip;
      this.pointline = new PointLine(this.showMap, this.pointLinelist)
      this.pointline.showLine()
      this.pointline.showPoint(this.pointLinelist)
    }
  },
  mounted () {
    _g.setChartHeight('asnmap', 'lineMap');
    this.getDataPoint()
    this.showMap.map.setTarget('lineMap')
    // 缩放连线消失
    let _this = this
    this.showMap.map.on('wheel', function () {
      let mapZoom = this.getView().getZoom();
      _this.pointline.removeLine()
      if (mapZoom >= 5) {
        console.log(_this)
        console.log(_this.pointline)
        _this.pointline.showChild(_this.pointLinelist)
        _this.pointline.removeLine()
      } else if (mapZoom > 0 && mapZoom < 5) {
        _this.pointline.showLine()
        _this.pointline.closeChild()
      }
    })
  }
}
</script>



