import * as types from '../mutation-types'
const state = {
  slideOpen: false,
  abilityQueryKeyword: 3361
}
// getters
const getters = {
  slideState: state => state.slideOpen,
  getAbilityQueryKeyword: state => state.abilityQueryKeyword
}
// actions
const actions = {
  setSlideState ({ commit, state }) {
    commit(types.SET_SLIDE_STATE)
  },
  closeSlide ({ commit, state }, klose) {
    commit(types.SET_SLIDE_STATE, klose)
  },
  changeAbilityKeyword ({commit, state}, keyword) {
    commit(types.SET_ABILITY_KEYWORD_STATE, keyword)
  }
}
// mutations
const mutations = {
  [types.SET_SLIDE_STATE] (state, klose) {
    state.slideOpen = typeof klose === 'undefined' ? !state.slideOpen : false
  },
  [types.SET_ABILITY_KEYWORD_STATE] (state, keyword) {
    state.abilityQueryKeyword = keyword
  }
}
export default {
  state,
  getters,
  actions,
  mutations
}



